import React from 'react';
import {connect} from 'react-redux';
import './FirstView.css';
import jetplane from './jetplane.png';  
import arrow2 from './arrow2.png';
import arrow1 from './arrow1.png';
import arrow3 from './arrow3.png';
import alien1 from './alien1.png';
import alien2 from './alien2.png';
import robot from './robot.png';



class FirstView extends React.Component {
	constructor(props) {
		super(props);
	}
	
	
	
	startEasyGame = () => {
		this.props.dispatch({type:'startGame',difficulty:1});
				
				
				
					
	}
	
	startMediumGame = () => {
		this.props.dispatch({type:'startGame',difficulty:1.3});
				
				
				
					
	}
	
	startHardGame = () => {
		this.props.dispatch({type:'startGame',difficulty:1.6});
				
				
				
					
	}
	
	componentDidMount(){
		document.getElementById('jet-plane').style.left=this.props.jetPosition.leftDistance;
		document.getElementById('jet-plane').style.bottom=this.props.jetPosition.bottomDistance;
		document.getElementById('easyButton').onclick=this.startEasyGame;
		document.getElementById('mediumButton').onclick=this.startMediumGame;
		document.getElementById('hardButton').onclick=this.startHardGame;
	}
	
	
	
	render() {
		let bugs=this.props.monsterCollection;
		let bugStyle= [
		{display:bugs.firstMonster.display, left:bugs.firstMonster.leftDistance,bottom:bugs.firstMonster.bottomDistance},
		{display:bugs.secondMonster.display, left:bugs.secondMonster.leftDistance,bottom:bugs.secondMonster.bottomDistance},
		{display:bugs.thirdMonster.display, left:bugs.thirdMonster.leftDistance,bottom:bugs.thirdMonster.bottomDistance},
		{display:bugs.fourthMonster.display, left:bugs.fourthMonster.leftDistance,bottom:bugs.fourthMonster.bottomDistance},
		{display:bugs.fifthMonster.display, left:bugs.fifthMonster.leftDistance,bottom:bugs.fifthMonster.bottomDistance},
		{display:bugs.sixthMonster.display, left:bugs.sixthMonster.leftDistance,bottom:bugs.sixthMonster.bottomDistance},
		{display:bugs.seventhMonster.display, left:bugs.seventhMonster.leftDistance,bottom:bugs.seventhMonster.bottomDistance},
		{display:bugs.eighthMonster.display, left:bugs.eighthMonster.leftDistance,bottom:bugs.eighthMonster.bottomDistance},
		{display:bugs.ninthMonster.display, left:bugs.ninthMonster.leftDistance,bottom:bugs.ninthMonster.bottomDistance},
		{display:bugs.tenthMonster.display, left:bugs.tenthMonster.leftDistance,bottom:bugs.tenthMonster.bottomDistance},
		{display:bugs.eleventhMonster.display, left:bugs.eleventhMonster.leftDistance,bottom:bugs.eleventhMonster.bottomDistance}
		]
		
		
		return (
		
			<div id='firstMainView' draggable='false'>
					
					<div id='gameAreaZero' draggable='false'>
					
						<div id='laserLine' draggable='false'></div>
						<img id='robot' draggable='false' src={robot}/>
						
					</div>
					
				<img id='firstMonster' className='bug' src={alien1} style={bugStyle[0]} draggable='false'/>
				<img id='secondMonster' className='bug' src={alien2} style={bugStyle[1]} draggable='false'/>
				<img id='thirdMonster' className='bug' src={alien1} style={bugStyle[2]} draggable='false'/>
				<img id='fourthMonster' className='bug' src={alien2} style={bugStyle[3]} draggable='false'/>
				
		
				<img id='fifthMonster' className='bug' src={alien2} style={bugStyle[4]} draggable='false'/>
				<img id='sixthMonster' className='bug' src={alien1} style={bugStyle[5]} draggable='false'/>
				<img id='seventhMonster' className='bug' src={alien2} style={bugStyle[6]} draggable='false'/>
				<img id='eighthMonster' className='bug' src={alien1} style={bugStyle[7]} draggable='false'/>
						 
					
				<img id='ninthMonster' className='bug' src={alien2} style={bugStyle[8]} draggable='false'/>
				<img id='tenthMonster' className='bug' src={alien1} style={bugStyle[9]} draggable='false'/>
				
			
				<img id='eleventhMonster' className='bug' src={alien1} style={bugStyle[10]} draggable='false'/>
				
				<img id='jet-plane' 
				src={jetplane} 
				alt='jet plane' 
				draggable='false'
				
				/>
				<div id='blocker'></div>
				<div id='buttonDiv'>
							<button id='easyButton'>EASY</button>
							<button id='mediumButton'>MEDIUM</button>
							<button id='hardButton'>HARD</button>
						</div>
			</div>		
				
	
			
		)
		
	}
	
}

function mapStateToProps(state){
	return {
			gameLevel:state.gameLevel,
			monsterCollection:state.monsterCollection,
			moveProperties:state.moveProperties,
			jetPosition:state.jetPosition,
			arrowPositions:state.arrowPositions,
	}
}
	

export default connect (mapStateToProps) (FirstView);
