import React from 'react';
import {connect} from 'react-redux';
import './LastView.css';
import jetplane from './jetplane.png';  
import arrow2 from './arrow2.png';
import arrow1 from './arrow1.png';
import arrow3 from './arrow3.png';
import alien1 from './alien1.png';
import alien2 from './alien2.png';
import robot from './robot.png';



class LastView extends React.Component {
	constructor(props) {
		super(props);
	}
	
	
	

	
	
	
	
	render() {
		let bugs=this.props.monsterCollection;
		let bugStyle= [
		{display:bugs.firstMonster.display, left:bugs.firstMonster.leftDistance,bottom:bugs.firstMonster.bottomDistance},
		{display:bugs.secondMonster.display, left:bugs.secondMonster.leftDistance,bottom:bugs.secondMonster.bottomDistance},
		{display:bugs.thirdMonster.display, left:bugs.thirdMonster.leftDistance,bottom:bugs.thirdMonster.bottomDistance},
		{display:bugs.fourthMonster.display, left:bugs.fourthMonster.leftDistance,bottom:bugs.fourthMonster.bottomDistance},
		{display:bugs.fifthMonster.display, left:bugs.fifthMonster.leftDistance,bottom:bugs.fifthMonster.bottomDistance},
		{display:bugs.sixthMonster.display, left:bugs.sixthMonster.leftDistance,bottom:bugs.sixthMonster.bottomDistance},
		{display:bugs.seventhMonster.display, left:bugs.seventhMonster.leftDistance,bottom:bugs.seventhMonster.bottomDistance},
		{display:bugs.eighthMonster.display, left:bugs.eighthMonster.leftDistance,bottom:bugs.eighthMonster.bottomDistance},
		{display:bugs.ninthMonster.display, left:bugs.ninthMonster.leftDistance,bottom:bugs.ninthMonster.bottomDistance},
		{display:bugs.tenthMonster.display, left:bugs.tenthMonster.leftDistance,bottom:bugs.tenthMonster.bottomDistance},
		{display:bugs.eleventhMonster.display, left:bugs.eleventhMonster.leftDistance,bottom:bugs.eleventhMonster.bottomDistance}
		]
		
		
		return (
		
			<div id='firstMainView' draggable='false'>
					
					<div id='gameArea' draggable='false'>
					
						<div id='laserLine' draggable='false'></div>
						<img id='robot' draggable='false' src={robot}/>
						
					</div>
					
				
				<div id='buttonDivLast'>
							<button id='playButton'>Play Again</button>
							<button id='portfolioButton'>See my portfolio</button>
							
						</div>
			</div>		
				
	
			
		)
		
	}
	
}

function mapStateToProps(state){
	return {
			gameLevel:state.gameLevel,
			monsterCollection:state.monsterCollection,
			moveProperties:state.moveProperties,
			jetPosition:state.jetPosition,
			arrowPositions:state.arrowPositions,
	}
}
	

export default connect (mapStateToProps) (LastView);
